.wrapper {
    background-color: white;
    border-radius: 5px;
    min-width: 300px;
    height: max-content;
    min-height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.container {
    margin: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 200px;
    /*align-items: start;*/
}

.buttonCheckout {
    width: 100%;
    height: 40px;
    background: red;
    color: white;
    font-size: 22px;
    font-family: "Roboto Light", monospace;
    border: none;
    border-radius: 5px;
    transition: 0.2s;
}

.buttonCheckout:hover {
    background: #de0000;
}

@media screen and (max-width: 600px) {
    .wrapper {
        width: 100%;
        height: min-content;
    }

    .wrapper p {
        font-size: 18px;
        height: 10%;
    }

    .container {
        height: min-content;
    }
}